* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.page {
  min-height: 100vh;
}
.page .content-area {
  width: calc(100% - 250px);
  min-height: 100%;
  transition: 0.3s;
  will-change: width;
  float: right;
}
.page .content-area.no-sidebar {
  width: 100%;
}

.page .sidebar {
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #2980b9;
  padding: 15px;
  transition: 0.3s;
}
.page .sidebar.no-sidebar {
  left: -250px;
}

.header i.toggle-sidebar {
  cursor: pointer;
}